@use "./../base/font" as ft;
@use "./../base/mediaquery" as md;
@use "./../base/color" as cl;
.section-title {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  @include md.sp {
    align-items: flex-start;
  }
  &__text {
    position: relative;
    @include ft.font2(bold, 48, 48, 0);
    color: cl.$dark;
    padding-right: 23.5px;
    margin-right: 19.5px;
    @include md.sp {
      font-size: calc(3.2rem + ((1vw - 3.75px) * 4.0816));
    }
    &::after {
      content: "";
      position: absolute;
      right: 0;
      bottom: 0;
      width: 2px;
      height: 42px;
      transform: rotate(40deg);
      background-color: cl.$base;
      @include md.sp {
        height: 32px;
      }
    }
  }
  &__translation {
    @include ft.font1(Medium, 24, 24, 0);
    margin-bottom: 5px;
    color: cl.$base;
    @include md.sp {
      font-size: calc(2rem + ((1vw - 3.75px) * 1.0204));
      margin-bottom: 0;
      margin-top: calc(1rem + ((1vw - 3.75px) * 1.0204));
    }
  }
  &--cl-lighter {
    .section-title {
      &__text {
        color: cl.$light;
        &::after {
          background-color: cl.$light;
        }
      }
      &__translation {
        color: cl.$light;
      }
    }
  }
  &--pc-position-left {
    justify-content: flex-start;
  }
}
